<template>
  <div class="file-upload-container">
    <img class="school-logo" src="@/assets/2.jpg" alt="badge">
    <el-button class="return" type="danger" @click="goBack"></el-button>
    <div class="header">
      <h1></h1>
      <p>DeepSeek AI</p>
    </div>

    <div class="upload-area" @dragover.prevent="onDragOver" @drop.prevent="onDrop">
      <div class="upload-content" :class="{ 'drag-active': isDragActive }">
        <el-icon class="upload-icon"><DocumentAdd /></el-icon>

        <h3></h3>
        <p></p>
        <p class="file-types">TXT, PDF, DOC, DOCX, XLSX, PPTX (1MB)</p>
        
        <el-upload
          ref="uploadRef"
          class="upload-input"
          action=""
          :auto-upload="false"
          :show-file-list="false"
          :on-change="handleFileChange"
          :accept="allowedFileTypes"
        >
          <el-button type="primary" style="width: 150px; height: 45px; font-size: 20px"></el-button>
        </el-upload>
      </div>
    </div>

    <div class="file-list" v-if="selectedFile">
      <div class="file-item">
        <el-icon><Document /></el-icon>
        <span class="file-name">{{ selectedFile.name }}</span>
        <span class="file-size">({{ formatFileSize(selectedFile.size) }})</span>
        <el-button type="danger"  @click="removeFile"></el-button>
      </div>
    </div>

    <div class="actions">
      <el-button 
        type="primary" 
        style="width: 200px; height: 50px; font-size: 24px"
        color="#0066CC"
        :disabled="!selectedFile" 
        @click="uploadFile"
        :loading="isUploading"
      >
        
      </el-button>
    </div>

    <div class="footer">
      <p> 2023  | DeepSeek AI</p>
    </div>
  </div>
</template>

<script setup>
import { ref } from 'vue'
import { DocumentAdd , Document } from '@element-plus/icons-vue'
import { useRouter } from 'vue-router' // 
import { ElMessage } from 'element-plus'
import { onMounted, onUnmounted } from 'vue'
import axios from 'axios'

const router = useRouter() // 
const uploadRef = ref(null)      // el-upload 
const isDragActive = ref(false)  //
const selectedFile = ref(null)   //
const isUploading = ref(false)   //

const allowedFileTypes = '.txt,.pdf,.doc,.docx,.xlsx,.pptx'   //
const maxFileSize = 1 * 1024 * 1024     // (20MB)

const preventDefault = (e) => {
  e.preventDefault()     ///
}

onMounted(() => {
  // 
  window.addEventListener('dragover', preventDefault, false)
  window.addEventListener('drop', preventDefault, false)
})

onUnmounted(() => {
  window.removeEventListener('dragover', preventDefault)
  window.removeEventListener('drop', preventDefault)
})

//
const onDragOver = () => {
  isDragActive.value = true               
}

//
//const onDragLeave = () => {
  //isDragActive.value = false            
//}

//
const onDrop = (e) => {                   
  isDragActive.value = false
  const files = e.dataTransfer.files
  if (files.length > 0) {
    handleFile(files[0])
  }
}

//el-upload 
const handleFileChange = (file) => {      
  handleFile(file.raw)
}

const handleFile = (file) => {
  // ()
  const fileExtension = file.name.split('.').pop().toLowerCase()
  const allowedExtensions = allowedFileTypes.split(',').map(ext => ext.replace('.', ''))
  
  if (!allowedExtensions.includes(fileExtension)) {
    ElMessage.error('')
    return
  }

  // 
  if (file.size > maxFileSize) {
    ElMessage.error('1MB')
    return
  }
  //
  selectedFile.value = file
}

//
const removeFile = () => {
  selectedFile.value = null
  if (uploadRef.value) {
    uploadRef.value.clearFiles()
  }
}

//("1.5 MB")(Bytes/KB/MB/GB)
const formatFileSize = (bytes) => {
  if (bytes === 0) return '0 Bytes'
  const k = 1024
  const sizes = ['Bytes', 'KB', 'MB', 'GB']
  const i = Math.floor(Math.log(bytes) / Math.log(k))
  return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i]
}

//
const uploadFile = async () => {
  if (!selectedFile.value) return

  isUploading.value = true
  ElMessage.success('30')
  try {
    // FormData
    const formData = new FormData()
    formData.append('file', selectedFile.value) // 
    
    // POST
    const response = await axios.post(
      '/api/process-file', //  
      formData,
      {
        headers: {
          'Content-Type': 'multipart/form-data' // Content-Type
        }
      }
    )

    // 
    if (response.status === 200) {
      ElMessage.success('...')
      
      // URL
      setTimeout(() => {
        window.location.href = 'http://47.115.45.110:7474/'
      }, 1500)
    } else {
      ElMessage.error(': ' + (response.data.message || ''))
    }
    
  } catch (error) {
    console.error(':', error)
    ElMessage.error(': ' + (error.response?.data?.message || error.message))
  } finally {
    isUploading.value = false
  }
}

//
const goBack = () => {
  //window.history.back(); // 
  // 
  router.go(-1);
};

</script>

<style scoped>

.school-logo {
    position: absolute;  /*  */
    top: 53.59px;          /*  20px */
    left: 32.26px;         /*  20px */
    width: 85.35px;        /*  */
    height: auto;       /*  */
    z-index: 10;        /*  */
  }

.return {
    position: absolute;  /*  */
    font-size: 16px;
    top: 74px;          /*  20px */
    right: 60px;         /*  20px */
    width: 100px;        /*  */
    height:45px;       /*  */
    z-index: 10;        /*  */
  }

.file-upload-container {
  max-width: 100%;
  margin: 0 auto;
  padding: 2rem;
  text-align: center;
}

.header {
  margin-bottom: 2rem;
  background-color:#409eff
}

.header h1 {
  font-size: 2rem;
  margin-bottom: 0.5rem;
  color: #333;
}

.header p {
  color: #fef6f6;
}

.upload-area {
  border: 2px dashed #dcdfe6;
  border-radius: 8px;
  padding: 2rem;
  margin-bottom: 1.5rem;
  transition: all 0.3s;
}

.upload-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}

.drag-active {
  border-color: #409eff;
  background-color: rgba(64, 158, 255, 0.05);
}

.upload-icon {
  font-size: 3rem;
  color: #316ff4;
}

.file-types {
  color: #909399;
  font-size: 0.9rem;
}

.upload-input {
  margin-top: 1rem;
}

.file-list {
  margin-bottom: 1.5rem;
}

.file-item {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.8rem;
  padding: 0.8rem;
  background-color: #f5f7fa;
  border-radius: 4px;
}

.file-name {
  font-weight: 500;
}

.file-size {
  color: #909399;
  font-size: 0.9rem;
}

.actions {
  margin-bottom: 2rem;
}

.footer {
  color: #909399;
  font-size: 0.9rem;
  margin-top: 2rem;
}
</style>